  .sample {
    .reg-is-wrap {
      padding: 30px 0;
    }

    .rq-tbl-wrap {
      width: 100%; max-width: 768px; margin: 0 auto;

      .reg-is-btn {
        display: flex; 
        justify-content: center; 
        margin-top: 20px; 
        gap: 0 10px;
      }

      .reg-is-btn .btn-is {
        display: flex; 
        justify-content: center; 
        align-items: center; 
        min-width: 100px; 
        height: 40px; 
        padding: 0 10px; 
        color: #fff; 
        font-size: 16px; 
        border-radius: 3px; 
        transition: 0.3s;
      }

      .reg-is-btn .btn-is:hover {
        opacity: 0.8;
      }

      .reg-is-btn .btn-is.confirm {
        background: #ff6600;
      }
      .reg-is-btn .btn-is.type1 {
        background: rgb(72, 72, 255);
      }

      .reg-is-btn .btn-is.type2 {
        background: yellowgreen;
      }    

      .hot-guide {
        text-align: right;
      }
    }

    .rq-tbl {
      width: 100%; 
      border-top: 1px solid #ddd;

      .rq-tbl-col01 {
        width: 30%; 
        min-width: 100px;
      }

      .rq-tbl-col02 {
        width: auto;
      }
      
      tr {
        border-bottom: 1px solid #ccc;
      }

      th {
        height: 50px; 
        padding: 5px 10px; 
        color: #333; 
        font-weight: 500; 
        text-align: left; 
        background: #f9f9f9;
      }

      td {
        position: relative; 
        padding: 5px 10px;
      }

      .label {
        display: block;
        position: relative;
        line-height: 1em;

        &.label-rq {

          &:after {
            content: '*'; 
            display: inline-block;
            color: #eb0000; 
            font-size: 14px; 
            line-height: 1;
          }
        }
      }

      .tbl-select {
        width: 100%; 
        height: 40px; 
        padding: 0 7px; 
        color: #333; 
        font-size: 14px; 
        border-radius: 3px; 
        border: 1px solid #ddd; 
        outline: none;

        &:focus {
          border-color: #aaa; transition: 0.3s;
        }
      }

      .inp-rq-wrap {
        width: 100%;
      }

      .inp-txt {
        width: 100%;
        height: 40px;
        padding: 0 10px;
        color: #333;
        font-size: 14px;
        border-radius: 3px;
        border: 1px solid #ddd;
        outline: none;
        transition: 0.3s;

        &:focus {
          border-color: #aaa;
        }

        &[disabled] {
          background-color: #ddd;
        }
      }

      .error-txt {
        max-height: 0;
        overflow: hidden;
        color: #eb0000;
        font-size: 12px;
        transition: 0.3s;

        &.visible {
          max-height: 100px;
        }

        &.multi-file {
          max-height: 100px;
        }
      }

      .g-txt {

        span {
          color: #eb0000;
          font-weight: 700;
        }
      }

      .tbl-select.is-empty,
      .tbl-ta.is-empty,
      .inp-txt.is-empty {
        border-color: #eb0000;

        &:focus {
          border-color: #eb0000;
        }

        & + .error-txt {
          max-height: 100px;
        }
      }

      .p-num {
        display: flex; 
        justify-content: space-between;
        gap: 0 5px;

        .tbl-select {
          width: 30%;
        }

        .inp-txt {
          width: calc(100% - calc(30% + 5px));
        }

        .rq-wrap {
          width: calc(100% - calc(30% + 5px));

          .inp-txt {
            width: 100%;
          }
        }
      }

      .tbl-ta-wrap {
        display: flex;
      }

      .tbl-ta {
        width: 100%; 
        height: 150px; 
        padding: 10px; 
        color: #333; 
        font-size: 14px; 
        border-radius: 3px; 
        border: 1px solid #ddd; 
        outline: none; 
        transition: 0.3s;
        resize: none;

        &:focus {
          border-color: #aaa;
        }
      }

      .rq-wrap {
        font-size: 0;

        &.full {
          width: 100%;
        }
      }

      .single-file-upload {

        .rq-wrap {
          width: 100%;
          margin-right: 5px;
        }
      }

      .email-row {
        display: flex;
        gap: 0 5px;

        & + .email-row {
          margin-top: 5px;
        }
      }

      .email-col-half {
        width: 50%;
      }

      .email-row.self {
        display: none; 
        margin-top: 5px;

        &.active {
          display: block;
        }
      }

      .cs-inp-file-wrap {

        .file-item {
          display: flex;
        }

        .cs-inp-file {
          display: none;
        }

        .cs-inp-file-txt {
          width: 100%;

          & + .btn-add-file {
            margin-left: 5px;
          }
        }

        .btn-remove-file {
          display: flex;
          justify-content: center;
          align-items: center;
          min-width: 80px;
          height: 40px;
          margin-left: 5px;
          background: #666;
          border-radius: 3px;
          color: #fff;
          font-size: 16px;

          &.hidden {
            display: none;
          }
  
          &:hover {
            opacity: 0.7;
          }
        }
      }

      .multi-file-upload {

        .file-item {
          margin-bottom: 5px;
        }
      }

      .btn-add-file {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 80px;
        height: 40px;
        background: #4848ff;
        border-radius: 3px;
        color: #fff;
        font-size: 16px;

        &:hover {
          opacity: 0.7;
        }
      }
    }

    .agree-box {
      padding: 10px;
      border: 1px solid #ddd;
      border-radius: 5px;

      .ab-txt {

        button {
          color: #0ba5ec;
          text-decoration: underline;
        }
      }
    }
  }

  .sp-md {
    display: none;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);

    &.active {
      display: flex;
    }

    &__mc {
      width: calc(100% - 30px);
      max-width: 450px;
      background: #fff;
      border-radius: 5px;
      overflow: hidden;
    }

    &__tit {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 40px;
      color: #fff;
      background-color: #04c259;
    }

    &__cont {
      max-height: 60vh;
      overflow-y: auto;
      padding: 15px;
    }

    &__btn {
      display: flex;
      justify-content: center;
      padding: 10px 0;

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        padding: 0 15px;
        color: #fff;
        background-color: #04c259;
        border-radius: 3px;
      }
    }
  }
  
  @media screen and (max-width: 767px) {
    .sample {

      .rq-tbl-wrap {

        .reg-is-btn .btn-is {
          min-width: 80px;
          height: 35px;
        }
      }

      .rq-tbl {

        .inp-txt {
          height: 35px;
          padding: 0 5px;
          font-size: 12px;
        }

        .tbl-select {
          height: 35px;
          padding: 0 2px;
          font-size: 12px;
        }
      }
      .rq-tbl {

        .label {
          font-size: 14px;
        }

        .btn-add-file {
          height: 35px;
          font-size: 14px;
        }

        .cs-inp-file-wrap {
          .btn-remove-file {
            height: 35px;
            font-size: 14px;
          }
        }
      }
      .rq-tbl td {
        font-size: 12px;
      }
    }
  }

  .sample-result {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 0 30px;

    .rb-txt1 {
      font-size: 1.5em;
      text-align: center;
    }

    .rb-txt2 {
      margin-top: 30px;
      font-size: 1.2em;
      text-align: center;
    }
  }